import * as THREE from "three";
// ÂØºÂÖ•ÊéßÂà∂Âô®
import { OrbitControls } from "three/examples/jsm/controls/OrbitControls";
// ÂØºÂÖ•Ê∞¥Èù¢
import { Water } from "three/examples/jsm/objects/Water2";
// ÂØºÂÖ•gltfËΩΩÂÖ•Â∫ì
import { GLTFLoader } from "three/examples/jsm/loaders/GLTFLoader";
import { DRACOLoader } from "three/examples/jsm/loaders/DRACOLoader";
import { RGBELoader } from "three/examples/jsm/loaders/RGBELoader";
// ÂàùÂßãÂåñÂú∫ÊôØ
const scene = new THREE.Scene();

// ÂàùÂßãÂåñÁõ∏Êú∫
const camera = new THREE.PerspectiveCamera(
  75,
  window.innerWidth / window.innerHeight,
  0.1,
  2000
);

// ËÆæÁΩÆÁõ∏Êú∫‰ΩçÁΩÆ
camera.position.set(-50, 50, 130);
// Êõ¥Êñ∞ÊëÑÂÉèÂ§¥ÂÆΩÈ´òÊØî‰æã
camera.aspect = window.innerWidth / window.innerHeight;
// Êõ¥Êñ∞ÊëÑÂÉèÂ§¥ÊäïÂΩ±Áü©Èòµ
camera.updateProjectionMatrix();

scene.add(camera);

// ÂàùÂßãÂåñÊ∏≤ÊüìÂô®
const renderer = new THREE.WebGLRenderer({
  // ËÆæÁΩÆÊäóÈîØÈΩø
  antialias: true,
  //   ÂØπÊï∞Ê∑±Â∫¶ÁºìÂÜ≤Âå∫
  logarithmicDepthBuffer: true,
});
renderer.outputEncoding = THREE.sRGBEncoding;

// ËÆæÁΩÆÊ∏≤ÊüìÂô®ÂÆΩÈ´ò
renderer.setSize(window.innerWidth, window.innerHeight);

// ÁõëÂê¨Â±èÂπïÁöÑÂ§ßÂ∞èÊîπÂèòÔºå‰øÆÊîπÊ∏≤ÊüìÂô®ÁöÑÂÆΩÈ´òÔºåÁõ∏Êú∫ÁöÑÊØî‰æã
window.addEventListener("resize", () => {
  camera.aspect = window.innerWidth / window.innerHeight;
  camera.updateProjectionMatrix();
  renderer.setSize(window.innerWidth, window.innerHeight);
});

// Â∞ÜÊ∏≤ÊüìÂô®Ê∑ªÂä†Âà∞È°µÈù¢
document.body.appendChild(renderer.domElement);

// ÂÆû‰æãÂåñÊéßÂà∂Âô®
const controls = new OrbitControls(camera, renderer.domElement);


// Ê∑ªÂä†Âπ≥Èù¢
// const planeGeometry = new THREE.PlaneGeometry(100, 100);
// const planeMaterial = new THREE.MeshBasicMaterial({
//   color: 0xffffff,
// });
// const plane = new THREE.Mesh(planeGeometry, planeMaterial);
// scene.add(plane);

// ÂàõÂª∫‰∏Ä‰∏™Â∑®Â§ßÁöÑÂ§©Á©∫ÁêÉ‰Ωì
let texture = new THREE.TextureLoader().load("/textures/sky.jpg");
const skyGeometry = new THREE.SphereGeometry(1000, 60, 60);
const skyMaterial = new THREE.MeshBasicMaterial({
  map: texture,
});
skyGeometry.scale(1, 1, -1);
const sky = new THREE.Mesh(skyGeometry, skyMaterial);

scene.add(sky);

// ËßÜÈ¢ëÁ∫πÁêÜ
const video = document.createElement("video");
video.src = "/textures/sky.mp4";
video.loop = true;

window.addEventListener("click", (e) => {
  // ÂΩìÈº†Ê†áÁßªÂä®ÁöÑÊó∂ÂÄôÊí≠ÊîæËßÜÈ¢ë
  //   Âà§Êñ≠ËßÜÈ¢ëÊòØÂê¶Â§Ñ‰∫éÊí≠ÊîæÁä∂ÊÄÅ
  if (video.paused) {
    video.play();
    let texture = new THREE.VideoTexture(video);
    skyMaterial.map = texture;
    skyMaterial.map.needsUpdate = true;
  }
});

// ËΩΩÂÖ•ÁéØÂ¢ÉÁ∫πÁêÜhdr
const hdrLoader = new RGBELoader();
hdrLoader.loadAsync("/assets/050.hdr").then((texture) => {
  texture.mapping = THREE.EquirectangularReflectionMapping;
  scene.background = texture;
  scene.environment = texture;
});

// Ê∑ªÂä†Âπ≥Ë°åÂÖâ
const light = new THREE.DirectionalLight(0xffffff, 1);
light.position.set(-100, 100, 10);
scene.add(light);

// ÂàõÂª∫Ê∞¥Èù¢
const waterGeometry = new THREE.CircleGeometry(300, 64);
const water = new Water(waterGeometry, {
  textureWidth: 1024,
  textureHeight: 1024,
  color: 0xeeeeff,
  flowDirection: new THREE.Vector2(1, 1),
  scale: 1,
  normalMap0: new THREE.TextureLoader().load("/textures/water/Water_1_M_Normal.jpg"),
  normalMap1: new THREE.TextureLoader().load("/textures/water/Water_2_M_Normal.jpg")
});
water.position.y = 3;
// Ê∞¥Èù¢ÊóãËΩ¨Ëá≥Ê∞¥Âπ≥
water.rotation.x = -Math.PI / 2;
scene.add(water);

// Ê∑ªÂä†Â∞èÂ≤õÊ®°Âûã
// ÂÆû‰æãÂåñgltfËΩΩÂÖ•Â∫ì
const loader = new GLTFLoader();
// ÂÆû‰æãÂåñdracoËΩΩÂÖ•Â∫ì
const dracoLoader = new DRACOLoader();
// Ê∑ªÂä†dracoËΩΩÂÖ•Â∫ì
dracoLoader.setDecoderPath("/draco/");
// Ê∑ªÂä†dracoËΩΩÂÖ•Â∫ì
loader.setDRACOLoader(dracoLoader);

loader.load("/model/island2.glb", (gltf) => {
  scene.add(gltf.scene);
});

function render() {
  // Ê∏≤ÊüìÂú∫ÊôØ
  renderer.render(scene, camera);
  // ÂºïÊìéËá™Âä®Êõ¥Êñ∞Ê∏≤ÊüìÂô®
  requestAnimationFrame(render);
}
render();